



































































































/*头部*/
.login .mint-header{
	background-color:#1b1b20;
	color: #fff;
	font-size: 0.8rem;
	z-index: 100;
	height: 2rem;
	line-height: 2.5rem;
}
/*返回按钮*/
.login button,.login button label{
	cursor:pointer;
	height: 1.7rem;
}
.login .mintui{
	font-size: 0.8rem;
}
/*登录组件*/
.login .loginForm section{
	line-height: 2rem;
	width: 100%;
	text-align: center;
	margin-top:1rem;
	margin-bottom: 1.2rem;
}
.login .loginForm section:nth-child(1){
	margin-top: 2rem;
}
.login .loginForm section div{
	width: 80%;
	border: 1px solid #e0e0e0;
	display: inline-block;
	background: #fff;
}
.login .loginForm section div input{
	outline: none;
	border: 0;
	font-size: 0.7rem;
	width: 90%;
}
/*登录按钮*/
.login .loginForm button{
	width: 80%;
	background: #ff3600;
	border-radius: 0px;
	color: #fff;
	text-align: center;
	margin-top: 1.2rem;
	height: 1.7rem;
	font-size: 0.7rem;
}
.login .test{
	font-size: 0.7rem;
}



