



















































































































	/*头部*/
.dragonDetail .mint-header{
		background-color:#1b1b20;
		color: #fff;
		font-size: .8rem;
		z-index: 100;
}
	/*返回按钮*/
.dragonDetail button,.dragonDetail button label{
		cursor:pointer;
		height: 1.7rem;
}
.swipe{
		height:10rem;
}
.banner{
	width:100%;
	height:10rem;
}
.cell{
  background-color: #fff;
  font-size: .7rem;
}
.cell-title{
	padding-top: 3%;
    padding-bottom: 3%;
	padding-left: 5%;
}
.cell-item{
  border-bottom:1px solid #ddd;
  padding:.8rem .8rem;
  overflow: hidden;
}
.cell-item p{
	overflow: hidden;
	text-overflow: ellipsis;
	white-space:nowrap;
}
.divider{
  height:.5rem;
  background: #f1f1f1;
  -webkit-box-shadow:0 5rpx 5rpx rgba(0,0,0,.1) inset;
          box-shadow:0 5rpx 5rpx rgba(0,0,0,.1) inset;
}
button::after{
  border-bottom: none;
  border-top: none;
  border-radius: 0;
}
.oldprice{
	float: left;
	text-decoration: line-through;
}
.newprice{
	float: right;
	margin-right: 1rem;
	color:red;
}
.cell-item p span{
	color:red;
	display: inline-block;
	border:1px solid red;
	border-radius: 5px;
	margin-left: 2px;
	padding: 0 3px;
}
.item-text{
	float: left;
	line-height: 1.6rem;
}
.numbox{
	float: right;
	margin-left: 1rem;
	margin-right: 1rem;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-flow: row;
	        flex-flow: row;
	border:1px solid #ccc;
	border-radius: 5px;
}
.numbox .numbtn{
	width: 1.6rem;
	background: #d3d3d3;
	color:#000;
	border:none;
}
.numbox .numinp{
	width:1.6rem;
	text-align: center;
}
.pay{
	float: right;
	margin-top:0.4rem;
	margin-right: 1rem;
	clear: both;
}
.argument{
	margin-bottom: 3rem;
}
.arg-btn{
	float: right;
}



