
a{color: #fff;text-decoration: none;
}
.droplist{position: absolute;top: 57px;left: 0;width: 100%;z-index: 10;-webkit-transition: .5s all;transition: .5s all;overflow: hidden;
}
.droplist .page-cell>a{background: #F2F2F2;
}
.page-back{
    display: inline-block;
    position: absolute;
    top: -8px;
    left: 10px;
    width: 40px;
    height: 40px;
    text-align: center;
}
.page-back i{    font-size: 24px;
    line-height: 40px;
}
.page-title{    font-size: 20px;
    margin: 20px auto;
    text-align: center;
    display: block;
    line-height: 1;
}



