
































































































































/*头部*/
.personal .mint-header{
	background-color:#1b1b20;
	color: #fff;
	font-size: 0.8rem;
	z-index: 100;
	height: 2rem;
	line-height: 2.5rem;
}
/*返回按钮*/
.personal button,.personal button label{
	cursor:pointer;
	height: 1.7rem;
}
.personal .mintui{
	font-size: 0.8rem;
}
/*个人信息*/
.personal .personalInfo{
	padding: 1.5rem 0.4rem;
	background: #ff3600;
}
.personal .personalInfo .Img{
	float: left;
	width: 20%;
	text-align: center;
}
.personal .personalInfo .Img img{
	width: 2.2rem;
	height: 2.2rem;
}
.personal .personalInfo .username{
	float: left;
	width: 45%;
}
.personal .personalInfo .username p{
	color: #fff;
	font-size: 0.7rem;
	font-weight: 500;
}
.personal .personalInfo .btn{
	float: left;
	margin-left: 0.5rem;
}
.personal .personalInfo .btn button{
	height: 1.7rem;
}
.personal .personalInfo .btn label{
	font-size: 0.6rem;
	color: #999;
	opacity: 0.5;
}
/*积分余额*/
.personal .accountInfo{
	background: #ff3600;
}
.personal .accountInfo ul li{
	float: left;
	list-style: none;
	color: #fff;
	font-size: 0.7rem;
	width: 33%;
	text-align: center;
	padding: 0.5rem 0;
	background: #b22700;
	margin-left: 0.5%;
}
.personal .accountInfo ul li:first-child{
	margin-left: 0;
}
/*我的订单*/
.personal .myOrder{
	font-size: 0.7rem;
	background: #fff;
}
.personal .myOrder .order{
	line-height: 3.7rem;
	border-bottom: 1px solid #ddd;
}
.personal .myOrder .btn{
	background: #828c98;
	color: #fff;
	border-radius: 2px;
	margin-left: 1rem;
	margin-right: 0.8rem;
	padding: 0 0.4rem;
	height: 1.5rem;
}
.personal .myOrder .btn i{
	font-size: 1.2rem;
}
.personal .myOrder .order .moreOrder{
	float: right;
	color: #c8c7cc;
	margin-right: 0.7rem;
}
.personal .myOrder .orderInfo .wait{
	float: left;
	width: 25%;
	text-align: center;
	margin-top: 0.8rem;
	margin-bottom: 0.8rem;
}
.personal .myOrder .orderInfo .wait i{
	font-size: 1.5rem;
	color: #424242;
}
.personal .myOrder .orderInfo .wait .text{
	color: #676767;
	padding-top: 0.5rem;
	font-size: 0.7rem;
}
/*收货地址等*/
.personal .otherInfo{
	font-size: 0.7rem;
	margin-top: 1rem;
	background: #fff;
	margin-bottom: 3rem;
}
.personal .otherInfo .address,
.personal .otherInfo .password,
.personal .otherInfo .phone,
.personal .otherInfo .login{
	line-height: 3.5rem;
	border-bottom: 1px solid #ddd;
}
.personal .otherInfo .login{
	border-bottom:0;
}
.personal .otherInfo .address .btn,
.personal .otherInfo .password .btn,
.personal .otherInfo .phone .btn,
.personal .otherInfo .login .btn{
	background: #51BCE0;
	color: #FFF;
	padding: 0 0.5rem;
	margin-left: 1rem;
	margin-right: 0.8rem;
	height: 1.8rem;
}
.personal .otherInfo .password .btn{
	background:#ff8c43;
}
.personal .otherInfo .phone .btn{
	padding: 0 0.7rem;
	background:#fa6c5e;
}
.personal .otherInfo .login .btn{
	padding: 0 0.5rem;
	background:#fa6c5e;
}
.personal .otherInfo .address .btn i,
.personal .otherInfo .password .btn i,
.personal .otherInfo .phone .btn i{
	font-size: 1.5rem;
}
.personal .otherInfo .login .btn i{
	font-size: 1.2rem;
}
.personal .otherInfo .right{
	float: right;
	margin-right: 0.8rem;
	color: #c8c7cc;
}



