
.clearfix::after{
	content: "";
	display: block;
	clear: both;
}
.product{
		background: #f4f4f4;
}
	/*头部	*/
.product .mint-header{
		background-color:#222;
		color: #fff;
		font-size: 0.8rem;
		z-index: 1000;
		height: 2rem;
		line-height: 2.5rem;
}
	/*返回按钮*/
.product button,.product button label{
		cursor:pointer;
		height: 1.7rem;
}
.product .mintui{
		font-size: 0.8rem;
}
	/*搜索*/
.product .search{
		width: 80%;
		position: relative;
		top:3rem;
		left: 10%;
		z-index: 1;
}
.product .search .mint-search{
		width: 100%;
		height: 100%;
}
.product .search .mint-search .mint-searchbar{
		background: #ededed;
}
.product .search .mint-search .mint-searchbar-inner{
		height: 1.3rem;
}
.product .search .mint-search .mint-searchbar-inner .mint-searchbar-core{
		font-size: 0.5rem;
}
.product .search .mint-searchbar-cancel{
		font-size: 0.5rem;
}
	/*茶品*/
.product .tea{
		position: relative;
		top:4rem;
		margin-bottom: 1rem;
}
.product .tea:last-child{
		margin-bottom: 3rem;
}
.product .tea h3{
		width: 100%;
		font-weight: 500;
		font-size: 1rem;
		line-height: 3rem;
		color: #10181f;
		text-align: center;
		line-height: 3rem;
}
.product .tea ul{
		width: 100%;
		text-align: center;
		background: #fff;
}
.product .tea ul li{
		float: left;
		width: 32.95%;
		list-style: none;
		border:1px solid #e0e0e0;
}
.product .tea ul li:nth-child(3n-2){
		border-left: 0;
}
.product .tea ul li:nth-child(3n){
		border-right: 0;
}
.product .tea ul li:nth-child(3n-2){
		border-left: 0;
}
.product .tea ul li p{
		width: 100%;
		text-align: center;
}
.product .tea ul li .text{
		padding-bottom: 10%;
		font-size: 0.5rem;
		color: #10181f;
}
.pro{
    width: 320px;
    border: 1px solid #eee;
    overflow: hidden;
		margin: 0 auto 20px;
    padding: 0 10px;
    background-color: #fff;
    position: relative;
    -webkit-transition: all .4s;
    transition: all .4s;
}
.pro>a{display: block;color: inherit;
    text-decoration: none;
    background-color: transparent;
}
.pro_img{padding: 10px 0;
    text-align: center;
}
.pro_img img{width: 260px;
    height: 260px;
    vertical-align: top;border-style: none;
}
.pro_name{margin-bottom: 10px;font-size: 15px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.pro_btm{    position: relative;
    margin: 10px 0 12px;
}
.pro_btm .fl{float: left;
}
.pro_btm .fr{float: right;margin-right: 20px;
}
.pro_btm .fl span:first-child{font-size: 14px;
}
.pro_btm .fl .new-price{font-size: 18px;font-weight: bold;color: #B4272D;
}
.pro_btm .fl .old-price{font-size: 12px;
    margin: 4px 0 0 6px;
    color: #999;
    text-decoration: line-through;
}
.pro_btm .fr{margin-top: 4px;color: #999;
    font-size: 14px;
}



