
.home{
	background: #fff;
	position: relative;
}
/*标题、搜索框、bars*/
.home header{
	width: 100%;
	height: 3rem;
	background: #ff3600;
	text-align: center;
}
.home header .title{
	float: left;
	font-size: 1.2rem;
	color: #fff;
	text-align: center;
	line-height: 3rem;
	margin-left: 0.3rem;
	font-family: "华文行楷";
	width: 30%;
}
.home header .search{
	float: left;
	width: 50%;
	text-align: center;
	margin-top: 0.2rem;
}
.home header .search input{
	font-size: 0.1rem;
}
.home .mint-search{
	height: 100%;
}
.home .mint-searchbar-inner{
	height: 1rem;
	margin-top: 2px;
}
.home .search .mint-searchbar{
	background-color: #ff3600;
	padding-right: 0px;
}
.home header .search .mint-searchbar-cancel{
	color: #fff;
	font-size: 0.1rem;
}
.home header .icon{
	width: 10%;
	float: right;
	margin-right: 0.5rem;
	text-align: center;
	font-size: 1rem;
	position: relative;top: 15px;
	color: #fff;
}
.icon .img1{position: absolute;top: -.4375rem;left: .625rem;width: 1.6rem;height:1.6rem;-webkit-transform-origin: center center;transform-origin: center center;-webkit-transition: .5s all ease-in-out;transition: .5s all ease-in-out;-webkit-transform: rotate(0deg);transform: rotate(0deg);
}
.icon .img2{position: absolute;top: 0.5rem;left: -2px;width: 1.2rem;height: 1.3rem;
}
.img_box{position: absolute;top: 0.5rem;left: -2px;width: 1.2rem;height: 1.3rem;overflow: hidden;
}
.icon .img3{width: 1.2rem;height: 1.3rem;-webkit-transition: .5s all ease-in-out;transition: .5s all ease-in-out;
}	
/*轮播图*/
.home .swipe{
	width: 100%;
	height: 11.2rem;
}
.home .swipe img{
	width: 100%;
	height: 100%;
}

/*担保信息*/
.home .part1 .text-content{
	width: 33%;
	height: 100%;
	line-height: 3rem;
	text-align: center;
	float: left;
}
.home .part1 .text-content .text{
	font-size: 0.5rem;
	color: #666;
}
.home .part1 .text-content .check-icon{
	color: #ff3600;
	font-weight: 200;
	font-size: 0.7rem;
}

/*XXXX馆*/
.home .part2,.home .part4{
	width: 100%;
	background: #f1f1f1;
	margin-bottom: 3rem;
}
.home .part2 .part2-content,
.home .part4 .part4-content{
	padding-top: 3%;
	padding-bottom: 3%;
}
.home .part2 .part2-img,
.home .part4 .part4-content .newProducts{
	width: 100%;
	background: #fff;
}
.home .part2 p{
	width: 45%;
	float: left;
	padding-left: 3%;
	padding-top: 3%;
	padding-bottom: 2.5%;
}
.home .part2 p img{
	width: 100%;
	height: 100%;
}

/*热销茶叶*/
.home .part3 h3,
.home .part4 .part4-content .newProducts h3{
	width: 100%;
	text-align: center;
	color: #10181f;
	font-weight: 300;
	margin-top: 0.8rem;
	font-size: 0.8rem;
}
.home .part3 p,
.home .part4 .part4-content .newProducts .text{
	color: #999;
	font-size: 0.5rem;
	width: 100%;
	text-align: center;
	padding-bottom: 3%;
	line-height: 1rem;
}
.home .part3 .list-img{
	width: 92%;
	margin-left: 4%;
}
.home .part3 div:last-child{
	margin-bottom:3%;
}
.home .part3 img{
	width: 100%;
	height: 100%;
}

/*人气新品*/
.home .part4 .part4-content .newProducts h3{
	margin-top: 0;
	padding-top: 0.8rem;
}
.home .part4 .product{
	width: 92%;
	margin-left: 4%;
	border-bottom: 1px solid #f1f1f1;
}
.home .part4 .product img{
	width: 100%;
	height: 100%;
	margin-top: 3%;
}
.home .part4 .product img:first-child{
	margin-top: 0px;
}
.home .part4 .newProducts .product p{
	font-size: 0.7rem;
	color: #10181f;
}
.home .part4 .newProducts .product .product-p1{
	padding-top: 3%;
}
.home .part4 .newProducts .product .product-p2{
	padding-bottom: 3%;
}
.home .part4 .newProducts .product p span{
	color: #555;
	font-size: 0.5rem;
}
.home .part4 .newProducts .product p .money{
	color: #ff3600;
	float: right;
}



